* {
  padding: 0;
  margin: 0;
  border: 0;
}

*,
*:before,
*:after {
  -moz-box-sizing: border-box;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
}

:focus,
:active {
  outline: none;
}

a:focus,
a:active {
  outline: none;
}

nav,
footer,
header,
aside {
  display: block;
}

html,
body {
  height: 100%;
  width: 100%;
  font-size: 100%;
  line-height: 1;
  font-size: 14px;
  -ms-text-size-adjust: 100%;
  -moz-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
}

input,
button,
textarea {
  font-family: inherit;
}

input::-ms-clear {
  display: none;
}

button {
  cursor: pointer;
}

button::-moz-focus-inner {
  padding: 0;
  border: 0;
}

a,
a:visited {
  text-decoration: none;
}

a:hover {
  text-decoration: none;
}

ul li {
  list-style: none;
}

img {
  vertical-align: top;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-size: inherit;
  font-weight: 400;
}

body {
  font-family: montserrat;
}

.container {
  width: 924px;
  margin: 0px auto;
}

.header__wrapper {
  margin-top: 15px;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.header__name {
  display: flex;
  align-items: center;
}

.header__name img:first-child {
  width: 40px;
  height: 40px;
}

.header__name img:last-child {
  width: 53.19px;
  height: 30px;
}

.header__nav ul {
  display: flex;
  align-items: center;
}

.header__nav ul li {
  font-size: 14px;
  line-height: 17px;
  color: rgba(31, 32, 65, 0.5);
}

.header__nav ul li button {
  border-radius: 22px;
  padding: 9px 20px 10px 20px;
  background-color: #fff;
  border: 3px solid #bc9cff;
}

.header__nav ul li button span {
  font-size: 12px;
  line-height: 15px;
  text-transform: uppercase;
  color: #bc9cff;
  font-family: montserrat-bold;
}

.header__nav ul li button:hover {
  background-color: #bc9cff;
}

.header__nav ul li button:hover span {
  color: #fff;
}

.header__nav ul li:not(:last-child) {
  padding-right: 20px;
}

